









































export default {
	data() {
		return {
			dc_info:'',
			account:'',
			// 支付状态现金 或 积分抵扣
			status:'0',
			// 是否选中积分抵扣
			isAcc:false,
			// 支付方式
			mode:0,
			money:0,
			dc_id:'',
			
		};
	},
	onLoad(option) {
		console.log(option.id)
		// 请求电池详情
		var that = this;
		// 存电池id
		uni.setStorageSync('dcId','option.id')
		that.dc_id = option.id;
		uni.showLoading({
			title:'加载中...'
		})
		uni.request({
			url:that.http+'index/getDcInfo',
			header:that.header,
			method:'POST',
			data:{
				dc_id:option.id
			},
			success: (result) => {
				// console.log(res)
				that.dc_info = result.data.data
				// 请求积分抵扣等
				uni.request({
					url:that.http+'order/getIntegralMax',
					method:'POST',
					header:that.header,
					data:{
						user_id:uni.getStorageSync('token')
					},
					success: (res) => {
						uni.hideLoading()
						// console.log(res)
						that.account= res.data.data;
						
					},
					fail() {
						uni.showToast({
							title:'网络延迟，请刷新后重试!',
							icon:'none'
						})
					}
				})
			},
			fail() {
				uni.showToast({
					title:'网络延迟，请刷新后重试!',
					icon:'none'
				})
			}
		})
	},
	methods:{
		next(){
			var that = this;
			uni.navigateTo({
				url:'../pw/pw'
			})
		},
		acc(){
			this.isAcc = !this.isAcc;
			if(this.isAcc){
				this.money = Number(this.dc_info.ya_money)+Number(this.account.money)
				console.log(this.money)
			}else{
				this.money = this.dc_info.ya_money
			}
		}
	}
}
